<template>
  <div class="mine">
    <div class="app-content-area">
      <div class="mine-content">
        <a class="mine-info">
          <div class="wave-area">
            <div class="wave-run wave-run1"></div>
            <div class="wave-run wave-run2"></div>
            <div class="wave-run wave-run3"></div>
          </div>
          <div class="info">
            <img src="../assets/icon_my.png">
            <p class="name">+34685917818</p>
          </div>
        </a>
        <div class="mine-divider"></div>
        <div class="disable-user-behavior">
          <div class="list">
              <a class="item-content">
                <img class="mine-list-icon" src="../assets/icon_direction.png">
                <p>我的收货地址</p>
              </a>
              <a class="item-content">
                <img class="mine-list-icon" src="../assets/icon_shop.png">
                <p>我的商家</p>
              </a>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  export default {
    name: 'index',
    mounted: function () {
      this.run1()
      this.run2()
      this.run3()
    },
    methods: {
      run1: function () {
        var _this = this
        $('.wave-run1').animate({
          left: '-2500px'
        }, 60000, 'linear', function () {
          $('.wave-run1').animate({
            left: 0
          }, 60000, 'linear', function () {
            _this.run1()
          })
        })
      },
      run2: function () {
        var _this = this
        $('.wave-run2').animate({
          left: 0
        }, 60000, 'linear', function () {
          $('.wave-run2').animate({
            left: '-2500px'
          }, 60000, 'linear', function () {
            _this.run2()
          })
        })
      },
      run3: function () {
        var _this = this
        $('.wave-run3').animate({
          left: '-2500px'
        }, 70000, 'linear', function () {
          $('.wave-run3').animate({
            left: 0
          }, 70000, 'linear', function () {
            _this.run3()
          })
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .mine {
    flex: 1;
  }

  .app-content-area {
    width: 100vw;
    margin: -1px auto;
    padding: 0 0 10px;
  }

  .mine-info {
    position: relative;
    z-index: 10;
    width: 100%;
    height: 50vw;
    min-height: 100px;
    max-height: 200px;
    background-color: rgba(27, 147, 222, .5);
    display: flex;
    display: -webkit-flex;
    -webkit-justify-content: center;
    -moz-justify-content: center;
    -ms-justify-content: center;
    -o-justify-content: center;
    justify-content: center;
    -webkit-align-items: center;
    -moz-align-items: center;
    -ms-align-items: center;
    -o-align-items: center;
    align-items: center;
    overflow: hidden;
  }

  .wave-area {
    width: 1500px;
    left: 0;
    bottom: -20px;
    overflow: hidden;
  }

  .wave-area,
  .wave-run {
    height: 200px;
    position: absolute;
  }

  .wave-run {
    width: 3000px;
    background: url(../assets/wave.png) center repeat-x;
    background-size: 1500px;
    opacity: .3;
    z-index: -1;
  }

  .wave-run1 {
    left: 0;
    bottom: 0;
  }

  .wave-run2 {
    left: -1000px;
    bottom: -10px;
  }

  .wave-run3 {
    left: -2000px;
    bottom: -20px;
  }

  .mine-info .info {
    width: 200px;
    text-align: center;
  }

  .mine-info img {
    margin: auto;
    width: 64px;
    height: 64px;
  }

  .mine-info .name {
    color: #fff;
    font-size: 18px;
    margin: 0;
  }

  .mine-divider {
    height: 10px;
  }

  .disable-user-behavior {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-touch-callout: none;
    -webkit-tap-highlight-color: transparent;
    -webkit-tap-highlight-color: transparent;
    -webkit-user-drag: none;
    -ms-touch-action: none;
    -ms-content-zooming: none;
  }

  .list {
    position: relative;
    padding-top: 1px;
    padding-bottom: 1px;
    padding-left: 0;
    margin-bottom: 20px;
  }

  .list:last-child {
    margin-bottom: 0;
  }

  .mine-content ion-item {
    height: 46px;
  }

  .item-complex,
  a.item.item-complex,
  button.item.item-complex {
    padding: 0;
  }

  .item,
  .item h1,
  .item h2,
  .item h3,
  .item h4,
  .item h5,
  .item h6,
  .item p,
  .item-content,
  .item-content h1,
  .item-content h2,
  .item-content h3,
  .item-content h4,
  .item-content h5,
  .item-content h6,
  .item-content p {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .item-content {
    padding: 11px;
    position: relative;
    z-index: 2;
    border: none;
    background-color: #fff;
    display: block;
    border-color: #ddd;
    margin: -1px;
    border-width: 1px;
    border-style: solid;
    color: #666;
    font-size: 14px;
  }

  .mine-content .mine-list-icon {
    float: left;
    height: 24px;
    margin-right: 5px;
    border-radius: 5px;
  }

  .mine-content .mine-list-icon+p {
    padding-left: 5px;
    line-height: 24px;
  }
</style>



