<template>
  <div class="company">
    <div class="title">
      首页
      <div class="button-addshop">添加商家</div>
    </div>
    <div class="home-content app-content-area">
      <div class="shoplist-area">
        <div class="shoplist-item" @click='prodectPic'>
          <div class="top">
            <img class="photo" src="https://img.freex.es/logo/019/1460409095.4181437">
            <h4 class="name">中汇文具</h4>
            <h6 class="description">电话: +34 918936681</h6>
          </div>
          <div class="clear-both">
            <div class="hr"></div>
            <div class="bot">
              <h6 class="info ng-binding">Dirección: Calle León, 15Pol.Cobo Calleja, Fuenlabrada</h6>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {
    mapActions
  } from 'vuex'
  export default {
    name: 'index',
    methods: {
      ...mapActions([ // 从store上绑定的getters中载入需要的到此组件中
        'fun' // 我们稍后在src/store/getters.js中创建一个名为index的方法
      ]),
      prodectPic: function () {
        this.$router.push({
          name: 'prodectPic'
        })
        this.fun(1)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .company {
    flex: 1
  }

  .button-addshop {
    position: absolute;
    width: 60px;
    height: 25px;
    line-height: 25px;
    font-size: 12px;
    color: #fff!important;
    margin-top: 4px;
    margin-right: 4px;
    border-radius: 3px;
    background-color: #0af;
    cursor: pointer;
    text-align: center;
    top: 5px;
    right: 5px;
  }

  .shoplist-item {
    display: inline-block;
    background-color: #fff;
    border-radius: 5px;
    width: 96%;
    height: 98%;
    margin: 2% 2% 0;
  }

  .shoplist-item .top img {
    float: left;
    width: 50px;
    height: 50px;
    border-radius: 7px;
    margin: 10px;
  }

  .shoplist-item .name {
    color: #000;
    padding-top: 10px;
    margin-bottom: 2.5px;
    font-weight: 400;
    font-size: 18px;
    margin-top: 10px;
  }

  .shoplist-item .description {
    color: #999;
    margin-top: 2.5px;
    padding-bottom: 10px;
    font-size: 12px;
    margin-bottom: 10px;
    font-weight: 500;
    font-family: "-apple-system", "Helvetica Neue", Roboto, "Segoe UI", sans-serif;
    line-height: 1.2;
  }

  .hr {
    border-bottom: 1px solid #eee;
    height: 1px;
    width: 100%;
    margin: 10px 0;
  }

  .shoplist-item .bot {
    padding-bottom: 10px;
  }

  .shoplist-item .info {
    padding: 10px;
    margin: 0;
    color: #555;
    font-size: 12px;
    font-weight: 500;
    font-family: "-apple-system", "Helvetica Neue", Roboto, "Segoe UI", sans-serif;
    line-height: 1.2;
  }
</style>



