import Vue from 'vue'
import Router from 'vue-router'
import login from '@/components/login'
import language from '@/components/language'
import country from '@/components/country'
import forget from '@/components/forget'
import recruit from '@/components/recruit'
import index from '@/components/index'
import company from '@/components/company'
import prodectPic from '@/components/prodectPic'
import search from '@/components/search'
import prodectNew from '@/components/new'
import hot from '@/components/hot'
import des from '@/components/des'
import ped from '@/components/ped'
import cart from '@/components/cart'
import order from '@/components/order'
import mine from '@/components/mine'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },
    {
      path: '/language',
      name: 'language',
      component: language
    },
    {
      path: '/country',
      name: 'country',
      component: country
    },
    {
      path: '/forget',
      name: 'forget',
      component: forget
    },
    {
      path: '/recruit',
      name: 'recruit',
      component: recruit
    },
    {
      path: '/index',
      name: 'index',
      component: index,
      children: [{
        path: '/',
        name: 'company',
        component: company
      },
      {
        path: '/prodectPic',
        name: 'prodectPic',
        component: prodectPic
      },
      {
        path: '/cart',
        name: 'cart',
        component: cart
      },
      {
        path: '/order',
        name: 'order',
        component: order
      },
      {
        path: '/mine',
        name: 'mine',
        component: mine
      },
      {
        path: '/search',
        name: 'search',
        component: search
      },
      {
        path: '/prodectNew',
        name: 'prodectNew',
        component: prodectNew
      },
      {
        path: '/hot',
        name: 'hot',
        component: hot
      },
      {
        path: '/des',
        name: 'des',
        component: des
      },
      {
        path: '/ped',
        name: 'ped',
        component: ped
      }]
    }
  ]
})



