<template>
  <div class="login">
    <div class="title">手机登录<span class="button-language" @click='language'>语言</span></div>
    <label @click="choiceCountry"><span class="input-label">国家</span><span class="item-content">{{country}}</span><i class="icon"></i></label>
    <label><span class="input-label">{{areaNub}}</span><input type="number" placeholder="请填写手机号码" name="" value=""></label>
    <label><span class="input-label">密码</span><input type="password" placeholder="请填写密码" name="" value=""></label>
    <div class="button-area">
      <button type="button" name="button" @click="index">登录</button>
    </div>
    <div class="button">
      <a href="javascript:;" @click="forget">忘记密码?</a>
      <a href="javascript:;" @click="recruit">用户注册</a>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'login',
    data () {
      return {
        country: '西班牙',
        areaNub: '+34'
      }
    },
    methods: {
      language: function () {
        this.$router.push({
          name: 'language'
        })
      },
      choiceCountry: function () {
        this.$router.push({
          name: 'country'
        })
      },
      forget: function () {
        this.$router.push({
          name: 'forget'
        })
      },
      recruit: function () {
        this.$router.push({
          name: 'recruit'
        })
      },
      index: function () {
        this.$router.push({
          name: 'index'
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .login {
    width: 100%;
    height: 100%;
    background: #f5f5f5;
    display: flex;
    flex-direction: column;
  }

  .title {
    height: 44px;
    text-align: center;
    background-color: #fff;
    font-size: 16px!important;
    font-weight: 500;
    line-height: 44px;
    position: relative;
  }

  .button-language {
    position: absolute;
    right: 6px;
    width: 100px;
    height: 44px;
    font-size: 16px;
    line-height: 44px;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #0c60ee;
    cursor: pointer;
    text-align: right;
  }

  .login label {
    display: flex;
    padding: 6px 0 5px 16px;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    background-color: #fff;
    color: #444;
    font-size: 16px;
    height: 34px;
    line-height: 34px;
  }

  .input-label {
    width: 80px;
    font-size: 14px;
  }

  .icon {
    min-width: 32px;
    color: #ccc;
    font-size: 16px;
    background: url('../assets/right.png') no-repeat center center;
    background-size: 16px;
  }

  .item-content {
    font-size: 14px;
    flex: 1;
  }

  .login label input {
    flex: 1;
    font-family: Arial;
    font-size: 14px;
    padding: 1px 0;
    border: 0;
    height: 34px;
    color: #111;
    vertical-align: middle;
    font-size: 14px;
    line-height: 16px;
  }

  .button-area {
    padding-left: 8px;
    padding-right: 8px;
  }

  button {
    width: 100%;
    margin-top: 10px;
    margin-bottom: 10px;
    border-color: transparent;
    background-color: #f8f8f8;
    color: #444;
    position: relative;
    display: inline-block;
    padding: 0 12px;
    min-width: 52px;
    min-height: 47px;
    border-width: 1px;
    border-style: solid;
    border-radius: 4px;
    vertical-align: top;
    text-align: center;
    text-overflow: ellipsis;
    font-size: 16px;
    line-height: 42px;
    cursor: pointer;
    background-color: #73cc32!important;
    color: #fff!important;
  }

  button:after {
    position: absolute;
    top: -6px;
    right: -6px;
    bottom: -6px;
    left: -6px;
    content: ' ';
  }

  .button {
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    padding: 0 12px;
    min-width: 52px;
  }

  .button a {
    color: rgba(0, 0, 0, .5);
    margin: 0;
    padding: 0;
    border: 0;
    vertical-align: baseline;
    text-decoration: none;
  }
</style>



