<template>
  <div class="prodectPic">
      <div class="title">
        中汇文具
        <img src="../assets/magnifier.png" alt="" @click="search">
      </div>
      <div class="nav-area">
        <a class="nav-item" @click="sort(0)">
          <img class="nav-image" src="../assets/icon_new.png">
          <div class="nav-title"><span>新到产品</span></div>
        </a>
        <a class="nav-item" @click="sort(1)">
          <img class="nav-image" src="../assets/icon_recoment.png">
          <div class="nav-title"><span>推荐产品</span></div>
        </a>
        <a class="nav-item" @click="sort(2)">
          <img class="nav-image" src="../assets/icon_sale.png">
          <div class="nav-title"><span>促销产品</span></div>
        </a>
        <a class="nav-item" @click="sort(3)">
          <img class="nav-image" src="../assets/icon_history.png">
          <div class="nav-title"><span>历史订购</span></div>
        </a>
      </div>
      <div class="list">
        <div class="item">
          <div class="categorylist-icon">
            <img src="https://img.freex.es/img/019/1000066610/150x105/00456fc18443fc501aeedafd036b6469">
          </div>
          <div class="middle"><p>OFERTA</p></div>
        </div>
      </div>
  </div>
</template>

<script>
  export default {
    name: 'prodectPic',
    methods: {
      search: function () {
        this.$router.push({
          name: 'search'
        })
      },
      sort: function (tabIndex) {
        switch (tabIndex) {
          case 0:
            this.$router.push({
              name: 'prodectNew'
            })
            break
          case 1:
            this.$router.push({
              name: 'hot'
            })
            break
          case 2:
            this.$router.push({
              name: 'des'
            })
            break
          case 3:
            this.$router.push({
              name: 'ped'
            })
            break
          default:

        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .prodectPic {
    flex: 1
  }

  .title img {
    position: absolute;
    top: 5px;
    right: 5px;
  }

  .nav-area {
    position: relative;
    margin-bottom: 5px;
    border-top: 0;
    border-bottom: 1px solid #ddd;
    background-color: #fff;
    width: 100%;
    overflow: hidden;
    display: table;
  }

  .nav-area .nav-item {
    display: inline-block;
    padding: 5px;
    background-color: #fff;
    border: none;
    width: 25%;
    box-sizing: border-box;
    text-align: center;
  }

  .nav-image {
    height: auto;
    width: 48px;
    max-width: 100%;
  }

  .list {
    position: relative;
    padding-top: 1px;
    padding-bottom: 1px;
    padding-left: 0;
    margin-bottom: 20px;
  }

  .list:last-child {
    margin-bottom: 0;
  }

  .item {
    padding: 5px;
    border-color: #ddd;
    background-color: #fff;
    color: #444;
    position: relative;
    z-index: 2;
    display: block;
    margin: -1px;
    border-width: 1px;
    border-style: solid;
    font-size: 16px;
  }

  .categorylist-icon {
    height: 70px;
    width: 100px;
    position: absolute;
    left: 5px;
    top: 5px;
  }

  .categorylist-icon img {
    height: 100%;
    width: 100%;
    object-fit: cover;
  }

  .item .middle {
    display: table-cell;
    vertical-align: middle;
    height: 70px;
    width: 100%;
    padding-left: 110px;
  }

  .item p {
    padding: 0 0 0 10px;
    margin: 0 0 10px;
    font-size: 16px;
    color: #000;
    display: -webkit-box;
    white-space: pre-wrap;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    word-break: break-all;
  }
</style>



