<template>
  <div class="cart">
    <div class="title">当前订单</div>
    <div class="box">
      <div>
        <img src="../assets/icon_cart.png">
        <p class="name">历史订购是空的</p>
        <p class="info">您目前没有订购任何产品</p>
        <p class="info">您可以点击左下角[产品图册]来下单!</p>
        <div class="href" @click='prodectPic'>去看看</div>
      </div>
    </div>
  </div>
</template>

<script>
  import {
    mapActions
  } from 'vuex'
  export default {
    name: 'index',
    methods: {
      ...mapActions([ // 从store上绑定的getters中载入需要的到此组件中
        'fun' // 我们稍后在src/store/getters.js中创建一个名为index的方法
      ]),
      prodectPic: function () {
        this.fun(1)
        this.$router.push({
          name: 'prodectPic'
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .cart {
    flex: 1;
    display: flex;
    flex-direction: column;
  }

  .box {
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .box div img {
    width: 100px;
    height: 100px;
    margin: 0 auto;
    display: block;
  }

  .box .name {
    color: #444;
    font-size: 15px;
    cursor: default;
    font-weight: 700;
    margin: 10px 0;
    text-align: center;
  }

  .info {
    color: #a2a2a2;
    font-size: 12px;
    cursor: default;
    padding: 0;
    margin: 0;
    text-align: center;
  }

  .href {
    margin-top: 10px;
    color: #0af;
    font-size: 15px;
    font-weight: 700;
    cursor: pointer;
    text-align: center;
  }
</style>



