<template>
  <div class="index">
    <router-view></router-view>
    <ul class="footer">
      <li v-show="index !== 0" @click='routerPush(0)'>
        <img src="../assets/star.png" alt="" id="star">
        <span>首页</span>
      </li>
      <li v-show="index === 0">
        <img src="../assets/starActive.png" alt="" id="star">
        <span class="active">首页</span>
      </li>
      <li v-show="index !== 1" @click='routerPush(1)'>
        <img src="../assets/nav2.png" alt="">
        <span>产品图册</span>
      </li>
      <li v-show="index === 1">
        <img src="../assets/navActive2.png" alt="">
        <span class="active">产品图册</span>
      </li>
      <li v-show="index !== 2" @click='routerPush(2)'>
        <img src="../assets/shopCar.png" alt="">
        <span>当前订单</span>
      </li>
      <li v-show="index === 2">
        <img src="../assets/shopCarActive.png" alt="">
        <span class="active">当前订单</span>
      </li>
      <li v-show="index !== 3" @click='routerPush(3)'>
        <img src="../assets/nav4.png" alt="">
        <span>订单管理</span>
      </li>
      <li v-show="index === 3">
        <img src="../assets/navActive4.png" alt="">
        <span class="active">订单管理</span>
      </li>
      <li v-show="index !== 4" @click='routerPush(4)'>
        <img src="../assets/nav5.png" alt="">
        <span>我的</span>
      </li>
      <li v-show="index === 4">
        <img src="../assets/navActive5.png" alt="">
        <span>我的</span>
      </li>
    </ul>
  </div>
</template>

<script>
  import {
    mapGetters,
    mapActions
  } from 'vuex'
  export default {
    name: 'index',
    computed: {
      ...mapGetters([ // 从store上绑定的getters中载入需要的到此组件中
        'index' // 我们稍后在src/store/getters.js中创建一个名为index的方法
      ])
    },
    methods: {
      ...mapActions([ // 从store上绑定的getters中载入需要的到此组件中
        'fun' // 我们稍后在src/store/getters.js中创建一个名为index的方法
      ]),
      routerPush: function (index) {
        var _this = this
        switch (index) {
          case 0:
            _this.fun(0)
            _this.$router.push({
              name: 'company'
            })
            break
          case 1:
            _this.fun(1)
            _this.$router.push({
              name: 'prodectPic'
            })
            break
          case 2:
            _this.fun(2)
            _this.$router.push({
              name: 'cart'
            })
            break
          case 3:
            _this.fun(3)
            _this.$router.push({
              name: 'order'
            })
            break
          case 4:
            _this.fun(4)
            _this.$router.push({
              name: 'mine'
            })
            break
          default:

        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .index {
    width: 100%;
    height: 100%;
    background: #f5f5f5;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .footer {
    display: flex;
    justify-content: space-around;
    color: #444;
    width: 100%;
    height: 49px;
    line-height: 49px;
    background: #f8f8f8;
    font-size: 12px;
    text-align: center;
  }

  li {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
  }

  img {
    width: 24px;
  }

  #star {
    width: 28px;
  }

  li span {
    height: 12px;
    line-height: 12px;
  }

  .active {
    color: #387ef5;
  }
</style>



