<template>
  <div class="language">
    <bar-header title="选择语言"></bar-header>
    <label>{{language}} 中文</label>
  </div>
</template>

<script>
  export default {
    name: 'language',
    data () {
      return {
        language: '中文'
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .language {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
  }

  label {
    border-color: #ddd;
    background-color: #fff;
    color: #444;
    position: relative;
    z-index: 2;
    display: block;
    margin: -1px;
    padding: 16px;
    border-width: 1px;
    border-style: solid;
    font-size: 16px;
  }
</style>



