// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'

Vue.config.productionTip = false

Vue.component('bar-header', {
  template: '<div class="bar-header"><span class="button" @click="back"></span>{{ title }}</div>',
  props: ['title'],
  methods: {
    back: function () {
      this.$router.go(-1)
    }
  }
})

Vue.component('back-header', {
  template: '<div class="title"><span class="button" @click="back"></span>{{title}}</div>',
  props: ['title'],
  methods: {
    back: function () {
      this.$router.go(-1)
    }
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  template: '<App/>',
  components: { App }
})



